import React from 'react'

// import AddUserDialog from './AddUserDialog'
import clsx from 'clsx'
import IconButton from '@material-ui/core/IconButton'
import { lighten, makeStyles } from '@material-ui/core/styles'
import Toolbar from '@material-ui/core/Toolbar'
import SettingsIcon from '@material-ui/icons/Settings';
import { useDispatch } from 'react-redux'
import * as modalRedux from '../../../_modal/_redux/modalRedux.jsx'
import GlobalFilter from '../../../../partials/table/GlobalFilter'
import ThemeButton from '../../../../partials/button/ThemeButton.jsx'


const useToolbarStyles = makeStyles(theme => ({
    
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        color: theme.palette.brand.main, 
        fontWeight: 'bold',
        flex: '1 1 100%',
    },
}))

const TableToolbar = props => {

      const dispatch = useDispatch();

  const callContactFormModal = () => {
    dispatch(modalRedux.actions.openModal('ContactProfileModal'));
  };
    const classes = useToolbarStyles()
    const {
        numSelected,
        preGlobalFilteredRows,
        setGlobalFilter,
        globalFilter,
    } = props
    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >


            <div className="d-flex bd-highlight w-100 align-items-center">
                <div className="mr-auto bd-highlight">        
               
                <h5 className="title color-brand"> Prospects <IconButton aria-label="delete" className={classes.margin}>
                    <SettingsIcon  />
                 </IconButton> </h5>

                </div>
                <div className="p-2 bd-highlight"> <GlobalFilter
                    preGlobalFilteredRows={preGlobalFilteredRows}
                    globalFilter={globalFilter}
                    setGlobalFilter={setGlobalFilter}
                />
                </div>
                <div className="p-2 bd-highlight">
                    <ThemeButton onClick={props.handleChange} classNames="darkGrey-button text-white"> Filter</ThemeButton>
                </div>
            </div>
        </Toolbar>
    )
}


export default React.memo(TableToolbar);
