import React, { Fragment, useEffect, useState } from "react";
import { Field, FieldArray } from "redux-form";
import TextInput from "../../../../partials/form/TextInput";
import { getAddressManagement } from "../../curd/prospectsCrud";
import { Col } from "react-bootstrap";
import { IconButton } from "@material-ui/core";
import RadioInput from "../../../../partials/form/RadioInput";

const RenderTable = ({
  fields,
  meta: { error, submitFailed },
  title,
  suburb,
  streets,
  streetNo,
  handleRowClick,
  placeholder
}) => {

  const [selectedRow, setSelectedRow] = useState(null);
  useEffect(() => {
    console.log(fields);
    getAddressManagement({
      suburb: suburb,
      streets: streets,
      streetNo: streetNo
    })
      .then(({ data }) => {
        fields.removeAll();
        data.forEach(function(element) {
          fields.push({ data: element });
        });
        console.log(data);
        return data;
      })
      .catch(() => {});
  }, [suburb,
    streets,
    streetNo]);



  const handleSelectAllToggle = (e) => {
    console.log("calleds");
    const all = fields.getAll();
    const selected = document.getElementById(`${fields.name}-checkToggle`)
      .checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].selected = selected;
      fields.push(item);
    });
  };

  const handleCLick = (data) => {
    console.log("called");
    setSelectedRow(data);
    handleRowClick({ type: fields.name, value: data });
  };
  return (
    <Fragment>
      <div  className={`tableTheme theme-table-striped ${selectedRow  && !streetNo ? "selected" : ""}`}>
        <table class="table">
          <thead>
            <tr>
              <th scope="col">
                <input
                  id={`${fields.name}-checkToggle`}
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              <th scope="col">{title}( {fields.length} )</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row"></th>
              <th scope="col" class="d-flex">
                <input
                  name={`${fields.name}`}
                  id={`${fields.name}`}
                  type="text"
                  placeholder={placeholder}
                  className="  form-control fix-rounded-right"
                  label={`Enter ${fields.name} name`}
                />
                <IconButton
                  onClick={() =>
                    fields.push({
                      data: document.getElementById(fields.name)?.value,
                    })
                  }
                  className="brand-icon-button"
                >
                  <i className="fas fa-plus-circle"></i>
                </IconButton>
              </th>
            </tr>
            {fields.map((item, index) => (
              <tr
                key={index}
                onClick={() => handleCLick(fields.get(index)?.data)}
                className= {(fields.get(index)?.data === selectedRow) && !streetNo ? "row-selected" : ""}
              >
                <th scope="row" onClick={(e) => e.stopPropagation()}>
                  <Field
                    name={`${item}.selected`}
                    component={RadioInput}
                    type="checkbox"
                  />
                </th>
                <td>{fields.get(index)?.data}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </Fragment>
  );
};
RenderTable.defaultProps = {
  suburb: "",
  streets: "",
  streetNo: ""
};
export default RenderTable;
