import React, { Fragment, useState, useCallback } from "react";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../partials/button/ThemeButton";
import { Row, Col } from "reactstrap";
import { reduxForm, FieldArray } from "redux-form";
import RenderTable from "./RenderTable";

const AddressManagement = (props) => {
  const [suburb, setSuburb] = useState(null);

  const [streets, setStreets] = useState(null);

  const [streetNo, setStreetNo] = useState(null);

  const handleRowClick = (evt) => {
    console.log(evt);
    if (evt.type === "suburbs") {
      setSuburb(evt.value);
      setStreets(null);
      setStreetNo(null);
    }
    if (evt.type === "streets") {
      setStreets(evt.value);
      setStreetNo(null);
    }
    if (evt.type === "streetNo") {
      setStreetNo(evt.value);
    }
  };

  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);


  const { handleSubmit, pristine, reset, submitting } = props;
  return (
    <Fragment>

<form onSubmit={handleSubmit(onFormSubmit)} >
      <ThemeHeader
        left={<h5 className="title">Address Management</h5>}
        right={
          <Fragment>
            <ThemeButton icon className="lightGrey-icon-button">
              <i className="fas fa-download"></i>
            </ThemeButton>
            <ThemeButton className="green-button mr-2">Import</ThemeButton>
            <ThemeButton disabled={pristine || submitting} type="submit" >Delete</ThemeButton>
          </Fragment>
        }
      />

        <Row>
          <Col md={3}>
            <FieldArray
              name="suburbs"
              title="Suburbs"
              handleRowClick={handleRowClick}
              component={RenderTable}
            />
          </Col>
          {suburb ? (  
            <Col md={3}>
              <FieldArray
                name="streets"
                title="Streets"
                placeholder="Enter a suburb here.."
                suburb={suburb}
                handleRowClick={handleRowClick}
                component={RenderTable}
              />
            </Col>
          ) : (
            ""
          )}

          {streets ? (
            <Col md={3}>
              <FieldArray
                name="streetNo"
                title="Street No."
                placeholder="Add a street here.."
                suburb={suburb}
                streets={streets}
                handleRowClick={handleRowClick}
                component={RenderTable}
              />
            </Col>
          ) : (
            ""
          )}

          {streetNo ? (
            <Col md={3}>
              <FieldArray
                name="unitNo"
                title="Unit No."
                placeholder="Enter a street No. "
                suburb={suburb}
                streets={streets}
                streetNo={streetNo}
                handleRowClick={handleRowClick}
                component={RenderTable}
              />
            </Col>
          ) : (
            ""
          )}
        </Row>
      </form>
    </Fragment>
  );
};

export default reduxForm({
  form: "addressManagement", // a unique identifier for this form
  initialValues: {},
})(AddressManagement);
