/* eslint-disable no-restricted-imports */
import React, { useCallback } from 'react'

// import AddUserDialog from './AddUserDialog'
import clsx from 'clsx'
import IconButton from '@material-ui/core/IconButton'
import { lighten, makeStyles } from '@material-ui/core/styles'
import Toolbar from '@material-ui/core/Toolbar'
import SettingsIcon from '@material-ui/icons/Settings';
import { useDispatch } from 'react-redux'
import * as modalRedux from '../../../_modal/_redux/modalRedux.jsx'
import * as prospectsRedux from '../../_redux/prospectsRedux'
import GlobalFilter from '../../../../partials/table/GlobalFilter'
import ThemeButton from '../../../../partials/button/ThemeButton.jsx'
import ToggleButtons from '../../../../partials/button/toggleButtons/ToggleButtons'
import { Form } from 'react-bootstrap'


const useToolbarStyles = makeStyles(theme => ({

    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        color: theme.palette.brand.main,
        fontWeight: 'bold',
        flex: '1 1 100%',
    },
}))

const TableToolbar = props => {

    const dispatch = useDispatch();

    const callAddNewZoneAnalysis = () => {
        dispatch(modalRedux.actions.openModal('AddNewZoneAnalysis'));
    };
    const radios = [
        { name: 'List', value: 'list' },
        { name: 'Map', value: 'map' }
    ];

    const classes = useToolbarStyles()

    const {
        numSelected,
        preGlobalFilteredRows,
        setGlobalFilter,
        globalFilter,
    } = props;

    const handleOnChange = (e) => {
        console.log(e.target.value)
        dispatch(prospectsRedux.actions.prospectZoneAnalysisFilter({options: e.target.value}));
    }
    
    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >


            <div className="d-flex bd-highlight w-100 justify-content-between">
                <div className="d-flex bd-highlight">

                    <h5 className=" color-brand"> Zone Analysis <IconButton aria-label="delete" className={classes.margin}>
                        <SettingsIcon />
                    </IconButton> </h5>
                    <div className="p-2 m-auto">
                        <ToggleButtons radios={radios} selectedValue={props.handleSelectedValue} initial="list" className="green-button text-white"/>
                    </div>
                    <div className="p-2 m-auto">
                        <Form.Control onChange={handleOnChange} as="select" custom>
                            <option value='all'>All</option>
                            <option value='mini-team'>Mini-Team</option>
                            <option value='self' >Self</option>
                        </Form.Control>
                    </div>
                </div>
                <div className="d-flex">
                    <div className="p-2">
                        <GlobalFilter
                            preGlobalFilteredRows={preGlobalFilteredRows}
                            globalFilter={globalFilter}
                            setGlobalFilter={setGlobalFilter}
                        />
                    </div>
                    <div className="p-2">
                        <ThemeButton onClick={callAddNewZoneAnalysis} className="green-button text-white"> Add Zone</ThemeButton>
                    </div>
                    <div className="p-2">
                        <ThemeButton onClick={props.handleChange} className="darkGrey-button text-white"> Filter</ThemeButton>
                    </div>
                </div>

            </div>
        </Toolbar>
    )
}


export default React.memo(TableToolbar);
