/* eslint-disable no-restricted-imports */
import React, { useState, Fragment } from "react";
import { useTable, useSortBy, useGlobalFilter } from "react-table";
import { Paper} from "@material-ui/core";
import TableToolbar from "./TableToolbar";
import { Waypoint } from "react-waypoint";
import { makeStyles } from "@material-ui/styles";
import * as prospectsRedux from "../../_redux/prospectsRedux";
import moment from "moment";
import { connect, useDispatch } from "react-redux";
import * as modalRedux from "../../../_modal/_redux/modalRedux";
import { currencyFormatter } from "../../../../_helpers/_utils/utils";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    overflowX: "auto",
    height: 300,
    flexGrow: 1,
  },
  head: {
    backgroundColor: theme.palette.brand.main,
    color: "#fff",
    position: "sticky",
    fontSize: ".6rem",
    top: 0,
  },
  table: {
    minWidth: 700,
    height: 200,
  },
  tableCell: {
    fontSize: ".6rem",
  },
}));

function Table({ columns, data, update, handleChange }) {
  const classes = useStyles();
  // Use the state and functions returned from useTable to build your UI

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    preGlobalFilteredRows,
    setGlobalFilter,
    prepareRow,
    state: { sortBy, globalFilter },
  } = useTable(
    {
      columns,
      data,
    },

    useGlobalFilter, // useGlobalFilter!
    useSortBy
  );
  console.log(rows);
  console.log(preGlobalFilteredRows);
  const dispatch = useDispatch();
  React.useEffect(() => {
    console.log("sort");
  }, [sortBy]);
  // Render the UI for your table
  const handleRowClicked = ({ original }) => {
    dispatch(
      modalRedux.actions.openModal("ProspectProfileModal", {
        prospectProfileId: original.id,
      })
    );
    console.log(original);
  };


  return (
    <Fragment>
      <TableToolbar
        handleChange={handleChange}
        numSelected={0}
        preGlobalFilteredRows={preGlobalFilteredRows}
        setGlobalFilter={setGlobalFilter}
        globalFilter={globalFilter}
      />

      <div className="tableFixHead theme-table-striped">
        <table {...getTableProps()} className="table">
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, i) => {
              prepareRow(row);
              console.log(row);
              return (
               
                <tr className="position-relative"
                  onClick={() => handleRowClicked(row)}
                  {...row.getRowProps()}
                >
                  {row.cells.map((cell) => {
                    return (
                      <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              );
            })}
            <tr>
              <td>
                <Waypoint onEnter={update} />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </Fragment>
  );
}

function ProspectsTable({ prospects, handleChange }) {
  const [items, setItems] = useState(prospects.entities);

  const columns = React.useMemo(
    () => [
      {
        Header: "Name",
        columns: [
          {
            Header: "Suburb",
            accessor: "suburb",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
              cell: data,
            }) => (
              <Fragment>
                {console.log(original)}
                {value}
              </Fragment>
            ),
          },
          {
            Header: "Street",
            accessor: "street_name",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Street",
            accessor: "street_number",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Unit",
            accessor: "unit",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Contact",
            accessor: "contact",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Phone",
            accessor: "phone",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
              cell: data,
            }) => (
              <a href={`tel:${value}`}>
                {value} {console.log(value)}
              </a>
            ),
          },
          {
            Header: "Price",
            accessor: "price",
            Cell: ({ cell: { value } }) => <p>{currencyFormatter(value, 0)}</p>,
          },
          {
            Header: "Status",
            accessor: "status", 
          Cell: ({ cell: { value } }) => <Fragment>{value.call? "":  <i className="fas fa-phone-slash"></i>}{value.house? <i className="fas fa-home"></i> :'' }{value.key? <i className="fas fa-key"></i> : ''}</Fragment>,
          },
          {
            Header: "Date",
            accessor: "date",
            Cell: ({ cell: { value } }) => (
              <p>{moment(value).format("DD/MM/YYYY")}</p>
            ),
          },
          {
            Header: "Time",
            accessor: "id",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
            }) => <p>{moment(original.date).format('LT')}</p>,
          },
          {
            Header: "Notes",
            accessor: "note",
            Cell: ({ cell: { value } }) => <p>{value.substring(0,15)}...</p>,
          },
        ],
      },
    ],
    []
  );
  console.log(prospects.entities);
  console.log(items);
  const fetchMoreData = () => {
    setTimeout(() => {
      console.log("called");
      setItems(prospects.entities.concat(items));
      // console.log('called')
    }, 1500);
  };

  const datas = React.useMemo(() => items, [items]);

  return (
    <Paper className="prospects-table">
      <Table
        columns={columns}
        data={datas}
        handleChange={handleChange}
        update={fetchMoreData}
      />
    </Paper>
  );
}

const mapStateToProps = ({ prospects }) => ({
  prospects: prospects,
});

// export default connect(mapStateToProps, prospectsRedux.actions)(ProspectsPage);
export default React.memo(
  connect(mapStateToProps, prospectsRedux.actions)(ProspectsTable)
);
