function Table({ columns, data, update, handleChange }) {
  const classes = useStyles();
  // Use the state and functions returned from useTable to build your UI

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    preGlobalFilteredRows,
    setGlobalFilter,
    prepareRow,
    footerGroups,
    state: { sortBy, globalFilter },
  } = useTable(
    {
      columns,
      data,
    },

    useGlobalFilter, // useGlobalFilter!
    useSortBy
  );
  console.log(rows);
  console.log(preGlobalFilteredRows);
  const dispatch = useDispatch();
  React.useEffect(() => {
    console.log("sort");
  }, [sortBy]);


  const [viewToggle, setViewToggle] = React.useState('list');

  // Render the UI for your table
  const handleRowClicked = ({ original }) => {
    dispatch(
      modalRedux.actions.openModal("ZoneAnalysis", {
        data: original,
      })
    );
    console.log(original);
  };

  const handleSelectedValue = (e) => {
    console.log(e)
    setViewToggle(e)
  }

  return (
    <Fragment>
      <TableToolbar
        handleChange={handleChange}
        handleSelectedValue={handleSelectedValue}
        numSelected={0}
        preGlobalFilteredRows={preGlobalFilteredRows}
        setGlobalFilter={setGlobalFilter}
        globalFilter={globalFilter}
      />
      {viewToggle === 'list' ? 
      <Fragment>
        <div className="tableFixHead theme-table-striped">
        <table {...getTableProps()} className="table">
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, i) => {
              prepareRow(row);
              return (
                <tr
                  className="position-relative"
                  onClick={() => handleRowClicked(row)}
                  {...row.getRowProps()}
                >
                  {row.cells.map((cell) => {
                    return (
                      <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              );
            })}
          </tbody>
          <tfoot>
            {footerGroups.map((group) => (
              <tr {...group.getFooterGroupProps()}>
                {group.headers.map((column) => (
                  <td {...column.getFooterProps()}>
                    {column.render("Footer")}
                  </td>
                ))}
              </tr>
            ))}
          </tfoot>
        </table>
      </div>
      </Fragment>

      : <ZoneAnalysisMap/>

      }

      
    </Fragment>
  );
}

